import { Component, OnInit, Renderer2, ViewChild, ElementRef } from '@angular/core';

import { User } from '../../models/user';
import { Momento } from '../../models/momento'
import { Conteudo } from '../../models/conteudo'
import { TipoConteudo } from '../../models/tipo_conteudo'
import { ConteudoService } from '../../services/conteudo.service'
import { FileService } from '../../services/file.service'
import { HostService } from '../../services/host.service'
import { Router } from '@angular/router';

import { MomentoService } from '../../services/momento.service';

declare const $: any;


@Component({
	selector: 'app-edicao-conteudo',
	templateUrl: './edicao-conteudo.component.html',
	styleUrls: ['./edicao-conteudo.component.css']
})
export class EdicaoConteudoComponent implements OnInit {

	@ViewChild("fileInput") fileInput:ElementRef;
	public conteudoAtual: Conteudo = new Conteudo();
	public flag: Boolean;


	constructor( private conteudoService: ConteudoService, private router: Router, private renderer: Renderer2,
		private fileService: FileService, private hostService: HostService, private momentoService: MomentoService) { }

	ngOnInit() {
		this.initComponent();

		// Code for the Validator
		const $validator = $('.wizard-card form').validate({
			rules: {
				conteudoTitulo: {
					required: true,
					minlength: 8
				}
			},

			errorPlacement: function(error: any, element: any) {
				$(element).parent('div').addClass('has-error');
			}
		});

	}

	
	initComponent(){

		this.fileService.resetService();
		this.flag = true;

		if(localStorage.getItem("currentIdConteudoEdit") != '')
			this.conteudoService.getConteudoById(+localStorage.getItem("currentIdConteudoEdit")).subscribe(
				conteudoResponse => { 
					this.conteudoAtual = JSON.parse(JSON.parse(JSON.stringify(conteudoResponse))._body);
					console.log("Momento sendo editado:");
					console.log(this.conteudoAtual);
					this.fileService.fileConteudoIsInside = true;
				} , error => console.log(error));
		else {
			this.conteudoAtual = new Conteudo();
			this.flag = false;
		} 

	}


	salvarConteudo(){
		//Validação força bruta pois, devido ao wizard, o JQuery validator não bloqueou o botão de submit
		if(this.conteudoAtual.titulo != null && this.conteudoAtual.titulo != ''){
			if(this.fileService.fileConteudoIsInside){

				(<HTMLInputElement> document.getElementById("salvar")).disabled = true;


				this.momentoService.getMomentoById(+localStorage.getItem("currentIdMomentoEdit")).subscribe(
					momentoResponse => {

						this.conteudoAtual.momento = JSON.parse(JSON.parse(JSON.stringify(momentoResponse))._body);

						this.fileService.upload().then(
							(result) => {
								let nomesArquivosSalvosTemp = localStorage.getItem("nomesArquivosSalvosTemp");

								console.log("Nomes dos arquivos salvos:")
								console.log(nomesArquivosSalvosTemp);
								let urlConteudo;
								let urlThumbsConteudo;

								if(nomesArquivosSalvosTemp.includes("conteudo:")){
									urlConteudo = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("conteudo:")+9, 
										nomesArquivosSalvosTemp.indexOf("/"));

									this.conteudoAtual.tipoConteudo = this.getTipoConteudo(urlConteudo);
									
									if(this.conteudoAtual.tipoConteudo == TipoConteudo.foto){
										urlThumbsConteudo = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/thumbs_conteudo:")+17, nomesArquivosSalvosTemp.lastIndexOf("/"));                    
										this.conteudoAtual.urlThumbs = urlThumbsConteudo;
									}

										
									this.conteudoAtual.url = urlConteudo;
									


								}

								this.conteudoService.saveConteudo(this.conteudoAtual).subscribe(
									conteudo => {
										(<HTMLInputElement> document.getElementById("salvar")).disabled = true;
										let conteudoResponse: Conteudo = JSON.parse(JSON.parse(JSON.stringify(conteudo))._body);
										this.conteudoAtual = new Conteudo();
										this.conteudoAtual.momento = conteudoResponse.momento;
										this.showNotificationNavegarParaTabelasConteudos('top','right', "Conteúdo Salvo com Sucesso!", 2);
									}, error => console.log(error));

							}, (error) => {
								console.error(error);
							});



					}, error=> console.log(error)
					);


			} else {
				this.showNotification('top','right', "Deve-se incluir uma foto para o conteúdo.", 3);

			}
		}
	}

	getTipoConteudo(filename: string): TipoConteudo{
		let tipoConteudo: TipoConteudo = /\.(jpg)$/i.test(filename) || /\.(jpeg)$/i.test(filename) || /\.(png)$/i.test(filename) ? 
		TipoConteudo.foto : /\.(mpeg)$/i.test(filename) || /\.(mpg)$/i.test(filename) || /\.(mp4)$/i.test(filename) ? 
		TipoConteudo.video: null;
		return tipoConteudo;
	}


	getUrlConteudo(){


		if(this.conteudoAtual){
			if(this.conteudoAtual.url  && (this.conteudoAtual.tipoConteudo == 0))
			{
				return this.hostService.host + this.conteudoAtual.url;
			} else if(!this.conteudoAtual.url || this.conteudoAtual.url == ''){
				return "../../assets/img/image_placeholder.jpg";
			}
			return "../../assets/img/thumbnail-video.png";
		}

		return "../../assets/img/image_placeholder.jpg";
	}


	showNotification(from: any, align: any, message: string, color: number) {

		const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

		/*const color = Math.floor((Math.random() * 6) + 1);*/

		$.notify({
			icon: 'notifications',
			message: message,
		}, {
			type: type[color],
			timer: 7000,
			delay: 10000,
			placement: {
				from: from,
				align: align
			},
			onClose: null
		});
	}

	showNotificationNavegarParaTabelasConteudos(from: any, align: any, message: string, color: number) {

		const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

		/*const color = Math.floor((Math.random() * 6) + 1);*/

		$.notify({
			icon: 'notifications',
			message: message,
		}, {
			type: type[color],
			timer: 3000,
			delay: 10000,
			placement: {
				from: from,
				align: align
			},
			onClosed: this.router.navigate(['/tables/datatables.net3'])
		});
	}

	navegarTabelaConteudos(){
		this.router.navigate(['/tables/datatables.net3']);
	}



	clearFotos(){
		if(this.fileInput.nativeElement)
			this.renderer.setAttribute(this.fileInput.nativeElement, 'value', '');
		this.fileService.resetService();
	}

	fileChangeEventWizard(fileInput: any, ouwner: string){

		if(!fileInput.target.files[0]){
			this.clearFotos();
			this.showNotification('top','right', "Deve-se incluir uma foto para o conteúdo.", 3);
		} else {
			if(!this.fileService.fileChangeEventWizard(fileInput, ouwner)){
				this.clearFotos();
				this.showNotification('top','right', "O aquivo deve ser uma foto ou vídeo.", 3);
			} 

		}

	}

	isEdit() {
		if(this.flag == false) {
			return "Adição";
		} else {
			return "Edição";
		}
	}

}



