import { Component, OnInit, OnDestroy,  OnChanges, AfterViewInit, SimpleChanges, Input } from '@angular/core';
import { DateAdapter } from '@angular/material';

import { User } from '../../models/user';
import { LocalStorageService }  from '../../services/local-storage.service';
import { UserService } from '../../services/user.service';
import { Momento } from '../../models/momento'
import { Conteudo } from '../../models/conteudo'
import { TipoConteudo } from '../../models/tipo_conteudo'
import { MomentoService } from '../../services/momento.service'
import { ConteudoService } from '../../services/conteudo.service'
import { FileService } from '../../services/file.service'
import { HostService } from '../../services/host.service'



declare const $: any;
interface FileReaderEventTarget extends EventTarget {
    result: string;
}
interface FileReaderEvent extends Event {
    target: FileReaderEventTarget;
    getMessage(): string;
}
@Component({
    selector: 'app-wizard-cmp2',
    templateUrl: 'wizard2.component.html',
    styleUrls: ['wizard2.component.css']
})

export class WizardComponent2 implements OnInit, OnChanges, AfterViewInit{

    public usuarioAtual: User;
    public momentoAtual: Momento = new Momento();
    public conteudoAtual: Conteudo = new Conteudo();
    public urlFotoMomento: string;

    constructor(private localStorageService: LocalStorageService, private userService: UserService,
        private momentoService: MomentoService, private conteudoService: ConteudoService, 
        private fileService: FileService, private hostService: HostService) {

        this.initComponent();
    }


    readURL(input: any) {
        if (input.files && input.files[0]) {
            const reader = new FileReader();

            reader.onload = function (e: FileReaderEvent) {
                $('#wizardPicturePreview').attr('src', e.target.result).fadeIn('slow');
            };
            reader.readAsDataURL(input.files[0]);
        }
    }

    initComponent(){

        this.fileService.resetService();

        if(this.localStorageService.componente_metodo.componente=='DataTableComponent2' &&
            this.localStorageService.componente_metodo.metodo=='editMomento' ) {

            this.momentoService.getMomentoById(+localStorage.getItem("currentIdMomentoEdit")).subscribe(
                momentoResponse => { 
                    this.momentoAtual = JSON.parse(JSON.parse(JSON.stringify(momentoResponse))._body);
                    console.log("Momento sendo editado:");
                    console.log(this.momentoAtual);
                    this.fileService.fileMomentoIsInside = true;
                } , error => console.log(error))

    }

    else {
        this.momentoAtual = new Momento(); 
        console.log('new');

    }

}

ngOnInit() {

    this.initComponent();

    // Code for the Validator
    const $validator = $('.wizard-card form').validate({
        rules: {
            titulo: {
                required: true,
                minlength: 3
            },
            descricao: {
                required: true,
                minlength: 3
            },
            data: {
                required: true,
                minlength: 8
            },
            conteudoTitulo: {
                required: true,
                minlength: 8
            },
            conteudoDescricao: {
                required: true,
                minlength: 8
            }
        },

        errorPlacement: function(error: any, element: any) {
            $(element).parent('div').addClass('has-error');
        }
    });

    // Wizard Initialization
    $('.wizard-card').bootstrapWizard({
        'tabClass': 'nav nav-pills',
        'nextSelector': '.btn-next',
        'previousSelector': '.btn-previous',
        'lastSelector':  '.btn-finish',

        onNext: function(tab: any, navigation: any, index: any) {
            const $valid = $('.wizard-card form').valid();
            if (!$valid) {
                $validator.focusInvalid();
                return false;
            }
        },

        onLast: function(tab: any, navigation: any, index: any) {
            const $valid = $('.wizard-card form').valid();
            if (!$valid) {
                $validator.focusInvalid();
                return false;
            }
        },

        onInit: function(tab: any, navigation: any, index: any){

            // check number of tabs and fill the entire row
            const $total = navigation.find('li').length;
            let  $width = 100 / $total;
            const $wizard = navigation.closest('.wizard-card');

            const $display_width = $(document).width();

            if ($display_width < 600 && $total > 3) {
                $width = 50;
            }

            navigation.find('li').css('width', $width + '%');
            const $first_li = navigation.find('li:first-child a').html();
            const $moving_div = $('<div class="moving-tab">' + $first_li + '</div>');
            $('.wizard-card .wizard-navigation').append($moving_div);

            // this.refreshAnimation($wizard, index);
            const total_steps = $wizard.find('li').length;
            let move_distance = $wizard.width() / total_steps;
            const step_width = move_distance;
            move_distance *= index;

            const $current = index + 1;

            if ($current === 1) {
                move_distance -= 8;
            } else if ($current === total_steps) {
                move_distance += 8;
            }

            $wizard.find('.moving-tab').css('width', step_width);
            $('.moving-tab').css({
                'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'

            });

            $('.moving-tab').css('transition', 'transform 0s');
        },

        onTabClick : function(tab: any, navigation: any, index: any){

            const $valid = $('.wizard-card form').valid();

            if (!$valid) {
                return false;
            } else {
                return true;
            }
        },

        onTabShow: function(tab: any, navigation: any, index: any) {
            const $total = navigation.find('li').length;
            let $current = index + 1;

            const $wizard = navigation.closest('.wizard-card');

            // If it's the last tab then hide the last button and show the finish instead
            if ($current >= $total) {
                $($wizard).find('.btn-next').hide();
                $($wizard).find('.btn-finish').show();
            } else {
                $($wizard).find('.btn-next').show();
                $($wizard).find('.btn-finish').hide();
            }

            const button_text = navigation.find('li:nth-child(' + $current + ') a').html();

            setTimeout(function(){
                $('.moving-tab').text(button_text);
            }, 150);

            const checkbox = $('.footer-checkbox');

            if ( index !== 0 ) {
                $(checkbox).css({
                    'opacity': '0',
                    'visibility': 'hidden',
                    'position': 'absolute'
                });
            } else {
                $(checkbox).css({
                    'opacity': '1',
                    'visibility': 'visible'
                });
            }

            // this.refreshAnimation($wizard, index);
            const total_steps = $wizard.find('li').length;
            let move_distance = $wizard.width() / total_steps;
            const step_width = move_distance;
            move_distance *= index;

            $current = index + 1;

            if ($current === 1) {
                move_distance -= 8;
            } else if ($current === total_steps) {
                move_distance += 8;
            }

            $wizard.find('.moving-tab').css('width', step_width);
            $('.moving-tab').css({
                'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'
            });
        }
    });


// Prepare the preview for profile picture
$('#wizard-picture').change(function(){
    this.readURL(this);
});

$('[data-toggle="wizard-radio"]').click(function(){
    const wizard = $(this).closest('.wizard-card');
    wizard.find('[data-toggle="wizard-radio"]').removeClass('active');
    $(this).addClass('active');
    $(wizard).find('[type="radio"]').removeAttr('checked');
    $(this).find('[type="radio"]').attr('checked', 'true');
});

$('[data-toggle="wizard-checkbox"]').click(function(){
    if ( $(this).hasClass('active')) {
        $(this).removeClass('active');
        $(this).find('[type="checkbox"]').removeAttr('checked');
    } else {
        $(this).addClass('active');
        $(this).find('[type="checkbox"]').attr('checked', 'true');
    }
});

$('.set-full-height').css('height', 'auto');

}

ngOnChanges(changes: SimpleChanges) {
    const input = $(this);
    const target: EventTarget = null;
    if (input.files && input.files[0]) {
        const reader: any = new FileReader();

        reader.onload = function (e: any) {
            $('#wizardPicturePreview').attr('src', e.target.result).fadeIn('slow');
        };
        reader.readAsDataURL(input.files[0]);
        

    }
}

ngAfterViewInit() {
    $('.wizard-card').each(function(){

        const $wizard = $(this);
        const index = $wizard.bootstrapWizard('currentIndex');
        // this.refreshAnimation($wizard, index);

        const total_steps = $wizard.find('li').length;
        let move_distance = $wizard.width() / total_steps;
        const step_width = move_distance;
        move_distance *= index;

        const $current = index + 1;

        if ($current === 1) {
            move_distance -= 8;
        } else if ($current === total_steps) {
            move_distance += 8;
        }

        $wizard.find('.moving-tab').css('width', step_width);
        $('.moving-tab').css({
            'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
            'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'

        });

        $('.moving-tab').css({
            'transition': 'transform 0s'
        });
    });
}

salvarMomentoEConteudo(){

    if(this.fileService.fileMomentoIsInside && this.fileService.fileConteudoIsInside){
        this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
            userResponse => {

                this.momentoAtual.user = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);;

                this.fileService.upload().then(
                    (result) => {
                        let nomesArquivosSalvosTemp = localStorage.getItem("nomesArquivosSalvosTemp");
                        console.log(nomesArquivosSalvosTemp);
                        let momento;
                        let conteudo;

                        if(nomesArquivosSalvosTemp.includes("momento:")){
                            momento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("momento:")+8, nomesArquivosSalvosTemp.indexOf("/"));
                            this.momentoAtual.urlFoto = momento;
                        }
                        
                        if(nomesArquivosSalvosTemp.includes("conteudo:")){
                            conteudo = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("conteudo:")+9, 
                                nomesArquivosSalvosTemp.lastIndexOf("/"));
                            this.conteudoAtual.url = conteudo;

                        }
                        
                        this.conteudoAtual.tipoConteudo = this.getTipoConteudo(conteudo);


                        console.log("momentoname:"+momento);
                        console.log("conteudoname:"+conteudo);
                        

                        this.momentoService.saveMomento(this.momentoAtual).subscribe(
                            momento => {
                                this.momentoAtual = JSON.parse(JSON.parse(JSON.stringify(momento))._body);
                                this.conteudoAtual.momento = this.momentoAtual;
                                this.conteudoService.saveConteudo(this.conteudoAtual).subscribe(
                                    conteudo => {
                                        this.conteudoAtual = JSON.parse(JSON.parse(JSON.stringify(conteudo))._body);
                                        alert("Momento e Conteúdo Salvos com Sucesso!");
                                    },
                                    error => console.log(error)
                                    );
                            }, error => console.log(error));

                    }, (error) => {
                        console.error(error);
                    });

            }, error => console.log(error)
            );



    } else {
        alert("Deve-se incluir uma foto para o momento e uma foto ou video no conteudo.")
    }
}

getTipoConteudo(nomeConteudo: string): TipoConteudo{
    let tipoConteudo: TipoConteudo = nomeConteudo.includes('.jpg') || nomeConteudo.includes('.png') || 
    nomeConteudo.includes('.jpeg') ? TipoConteudo.foto : TipoConteudo.video;
    return tipoConteudo;
}


getUrlFotoMomento(){

    if(this.momentoAtual)
        if(this.momentoAtual.urlFoto)
        {

            /*console.log("UrL"+this.hostService.host + this.momentoAtual.urlFoto);*/
            return this.hostService.host + this.momentoAtual.urlFoto;

        }

        return "../../assets/img/image_placeholder.jpg";
        
    }


}





