import { Component, ElementRef, Renderer2, OnInit, ViewChild} from '@angular/core'
import { Router } from '@angular/router';

import { MomentoService } from '../../services/momento.service'
import { FileService } from '../../services/file.service'
import { HostService } from '../../services/host.service'

import { UserService } from '../../services/user.service';

import { Momento } from '../../models/momento'



declare const $: any;


@Component({
	selector: 'app-edicao-narrativa',
	templateUrl: './edicao-narrativa.component.html',
	styleUrls: ['./edicao-narrativa.component.css']
})
export class EdicaoNarrativaComponent implements OnInit {

	public momentoAtual: Momento = new Momento();
	@ViewChild("fileInput") fileInput:ElementRef;
	public flag: Boolean;



	constructor( private momentoService: MomentoService, private router: Router, private renderer: Renderer2,
		private fileService: FileService, private hostService: HostService, private userService: UserService) { }


	ngOnInit() {
		this.initComponent();

		// Code for the Validator
		const $validator = $('.wizard-card form').validate({
			rules: {
				titulo: {
					required: true,
				},
				descricao: {
					required: true,
					minlength: 3
				},
				data1: {
					required: true,
				},
				data2: {
					required: true,
				},

			},

			errorPlacement: function(error: any, element: any) {
				$(element).parent('div').addClass('has-error');
			}
		});

	}

	
	initComponent(){

		this.fileService.resetService();
		if(localStorage.getItem("currentIdMomentoEdit") != '')
			this.momentoService.getMomentoById(+localStorage.getItem("currentIdMomentoEdit")).subscribe(
				momentoResponse => { 
					this.momentoAtual = JSON.parse(JSON.parse(JSON.stringify(momentoResponse))._body);
					this.fileService.fileMomentoIsInside = true;
				} , error => console.log(error));
		else {
			this.momentoAtual = new Momento();
			this.flag = false;
		}

	}

	validaCamposVazios(): Boolean{
		if(this.momentoAtual.dataInicial  &&
			this.momentoAtual.dataFinal && 
			this.momentoAtual.titulo != null && this.momentoAtual.titulo != '' &&
			this.momentoAtual.descricao != null && this.momentoAtual.descricao != '' 
			)
			return true;
		return false;
	}


	salvarMomento(fileInput: any){

		//Validação força bruta pois, devido ao wizard, o JQuery validator não bloqueou o botão de submit
		if(this.validaCamposVazios()){

			if(this.fileService.fileMomentoIsInside){

				(<HTMLInputElement> document.getElementById("salvar")).disabled = true;

				this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
					userResponse => {

						this.momentoAtual.user = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);


						this.fileService.upload().then(
							(result) => {
								let nomesArquivosSalvosTemp = localStorage.getItem("nomesArquivosSalvosTemp");
								let urlFotoMomento;
								let urlThumbsFotoMomento;

								if(nomesArquivosSalvosTemp.includes("momento:")){
									urlFotoMomento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("momento:")+8, nomesArquivosSalvosTemp.indexOf("/"));
									urlThumbsFotoMomento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/thumbs_momento:")+16, nomesArquivosSalvosTemp.lastIndexOf("/"));                    
									this.momentoAtual.urlFoto = urlFotoMomento;
									this.momentoAtual.urlThumbsFoto = urlThumbsFotoMomento;
									
								}

								this.momentoService.saveMomento(this.momentoAtual).subscribe(
									momento => {
										(<HTMLInputElement> document.getElementById("salvar")).disabled = true;
										let momentoResponse: Momento = JSON.parse(JSON.parse(JSON.stringify(momento))._body);
										this.momentoAtual = new Momento();
										this.fileService.resetService();
										this.momentoAtual.user = momentoResponse.user;
										this.showNotificationNavegarParaTabelasMomentos('top','right', "Memória Salvo com Sucesso!", 2);
									}, error => console.log(error));

							}, (error) => {
								console.error(error);
							});




					}, error=> console.log(error));



			} else {
				this.showNotification('top','right', "Deve-se incluir uma foto para a memória.", 3)

			}
		}

	}

	getUrlFotoMomento(){

		if(this.momentoAtual){
			if(this.momentoAtual.urlFoto)
			{
				return this.hostService.host + this.momentoAtual.urlFoto;
			}

			return "../../assets/img/image_placeholder.jpg";

		}
	}

	showNotification(from: any, align: any, message: string, color: number) {

		const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

		/*const color = Math.floor((Math.random() * 6) + 1);*/

		$.notify({
			icon: 'notifications',
			message: message,
		}, {
			type: type[color],
			timer: 7000,
			delay: 10000,
			placement: {
				from: from,
				align: align
			},
			onClose: null
		});
	}

	showNotificationNavegarParaTabelasMomentos(from: any, align: any, message: string, color: number) {

		const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

		/*const color = Math.floor((Math.random() * 6) + 1);*/

		$.notify({
			icon: 'notifications',
			message: message,
		}, {
			type: type[color],
			timer: 3000,
			delay: 10000,
			placement: {
				from: from,
				align: align
			},
			onClosed: this.navegarTabelaMomentos()
		});
	}

	navegarTabelaMomentos(){
		this.router.navigate(['/tables/datatables.net2']);
	}


	clearFotos(){
		if(this.fileInput.nativeElement)
			this.renderer.setAttribute(this.fileInput.nativeElement, 'value', '');
		this.fileService.resetService();
	}

	fileChangeEventWizard(fileInput: any, ouwner: string){

		if(!fileInput.target.files[0]){
			this.clearFotos();
			this.showNotification('top','right', "Deve-se incluir uma foto para a memória.", 3)
		} else {
			if(!this.fileService.fileChangeEventWizard(fileInput, ouwner)){
				this.clearFotos();
				this.showNotification('top','right', "O aquivo deve ser uma foto.", 3);
			} 

		}

	}

	isEdit() {
		if(this.flag == false) {
			return "Adição";
		} else {
			return "Edição";
		}
	}

}







