import { Component, ElementRef, Renderer2, OnInit, ViewChild} from '@angular/core'
import { Router } from '@angular/router';

import { TipoEvento } from '../../models/tipo_evento'
import { FileService } from '../../services/file.service'
import { HostService } from '../../services/host.service'

import { UserService } from '../../services/user.service';

import { Momento } from '../../models/momento'
import { User } from '../../models/user'



declare const $: any;


@Component({
	selector: 'app-edicao-usuario',
	templateUrl: './edicao-usuario.component.html',
	styleUrls: ['./edicao-usuario.component.css']
})
export class EdicaoUsuarioComponent implements OnInit {

	@ViewChild("fileInput") fileInput:ElementRef;
	public flag: Boolean;
	public user: User = new User();



	constructor( private router: Router, private renderer: Renderer2,
		private fileService: FileService, private hostService: HostService, private userService: UserService) { }


	ngOnInit() {

		this.initComponent();

		// Code for the Validator
		const $validator = $('.wizard-card form').validate({
			rules: {
				titulo: {
					required: true,
				},
				descricao: {
					required: true,
					minlength: 3
				},
				data1: {
					required: true,
				},
				data2: {
					required: true,
				},

			},

			errorPlacement: function(error: any, element: any) {
				$(element).parent('div').addClass('has-error');
			}
		});

		        // Wizard Initialization
        $('.wizard-card').bootstrapWizard({
            'tabClass': 'nav nav-pills',
            'nextSelector': '.btn-next',
            'previousSelector': '.btn-previous',

            onNext: function(tab: any, navigation: any, index: any) {
                const $valid = $('.wizard-card form').valid();
                if (!$valid) {
                    $validator.focusInvalid();
                    return false;
                }
            },

            onInit: function(tab: any, navigation: any, index: any){

                // check number of tabs and fill the entire row
                const $total = navigation.find('li').length;
                let  $width = 100 / $total;
                const $wizard = navigation.closest('.wizard-card');

                const $display_width = $(document).width();

                if ($display_width < 600 && $total > 3) {
                    $width = 50;
                }

                navigation.find('li').css('width', $width + '%');
                const $first_li = navigation.find('li:first-child a').html();
                const $moving_div = $('<div class="moving-tab">' + $first_li + '</div>');
                $('.wizard-card .wizard-navigation').append($moving_div);

                // this.refreshAnimation($wizard, index);
                const total_steps = $wizard.find('li').length;
                let move_distance = $wizard.width() / total_steps;
                const step_width = move_distance;
                move_distance *= index;

                const $current = index + 1;

                if ($current === 1) {
                    move_distance -= 8;
                } else if ($current === total_steps) {
                    move_distance += 8;
                }

                $wizard.find('.moving-tab').css('width', step_width);
                $('.moving-tab').css({
                    'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                    'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'

                });

                $('.moving-tab').css('transition', 'transform 0s');
            },

            onTabClick : function(tab: any, navigation: any, index: any){

                const $valid = $('.wizard-card form').valid();

                if (!$valid) {
                    return false;
                } else {
                    return true;
                }
            },

            onTabShow: function(tab: any, navigation: any, index: any) {
                const $total = navigation.find('li').length;
                let $current = index + 1;

                const $wizard = navigation.closest('.wizard-card');

                // If it's the last tab then hide the last button and show the finish instead
                if ($current >= $total) {
                    $($wizard).find('.btn-next').hide();
                    $($wizard).find('.btn-finish').show();
                } else {
                    $($wizard).find('.btn-next').show();
                    $($wizard).find('.btn-finish').hide();
                }

                const button_text = navigation.find('li:nth-child(' + $current + ') a').html();

                setTimeout(function(){
                    $('.moving-tab').text(button_text);
                }, 150);

                const checkbox = $('.footer-checkbox');

                if ( index !== 0 ) {
                    $(checkbox).css({
                        'opacity': '0',
                        'visibility': 'hidden',
                        'position': 'absolute'
                    });
                } else {
                    $(checkbox).css({
                        'opacity': '1',
                        'visibility': 'visible'
                    });
                }

                // this.refreshAnimation($wizard, index);
                const total_steps = $wizard.find('li').length;
                let move_distance = $wizard.width() / total_steps;
                const step_width = move_distance;
                move_distance *= index;

                $current = index + 1;

                if ($current === 1) {
                    move_distance -= 8;
                } else if ($current === total_steps) {
                    move_distance += 8;
                }

                $wizard.find('.moving-tab').css('width', step_width);
                $('.moving-tab').css({
                    'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                    'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'
                });
            }
        });


// Prepare the preview for profile picture
$('#wizard-picture').change(function(){
    this.readURL(this);
});

$('[data-toggle="wizard-radio"]').click(function(){
    const wizard = $(this).closest('.wizard-card');
    wizard.find('[data-toggle="wizard-radio"]').removeClass('active');
    $(this).addClass('active');
    $(wizard).find('[type="radio"]').removeAttr('checked');
    $(this).find('[type="radio"]').attr('checked', 'true');
});

$('[data-toggle="wizard-checkbox"]').click(function(){
    if ( $(this).hasClass('active')) {
        $(this).removeClass('active');
        $(this).find('[type="checkbox"]').removeAttr('checked');
    } else {
        $(this).addClass('active');
        $(this).find('[type="checkbox"]').attr('checked', 'true');
    }
});

$('.set-full-height').css('height', 'auto');

	}

	setTipoEvento(tipoEvento: string){
        console.log(tipoEvento);
        switch(tipoEvento) {
            case 'casamento':
            this.user.tipoEvento = TipoEvento.casamento;
            break;
            case 'disney':
            this.user.tipoEvento = TipoEvento.disney;
            break;
            case 'formatura':
            this.user.tipoEvento = TipoEvento.formatura;
            break;
        }
        console.log(this.user.tipoEvento);

    }


    salvarUsuario(){

    	(<HTMLInputElement> document.getElementById("salvar")).disabled = true;

        if(this.fileService.fileIsInside){
            this.fileService.upload().then(
                (result) => {
                    let nomesArquivosSalvosTemp = localStorage.getItem("nomesArquivosSalvosTemp");
                    let userFoto = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("usuario:")+8, nomesArquivosSalvosTemp.indexOf("/"));
                    let userFotoThumbs = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/thumbs_usuario:")+16, nomesArquivosSalvosTemp.lastIndexOf("/fundo:"));                  
                    let userFundo = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/fundo:")+7, nomesArquivosSalvosTemp.lastIndexOf("/thumbs_fundo:"));
                    console.log("fundo:");
                    console.log(nomesArquivosSalvosTemp);
                    console.log(userFundo);

                    console.log('url foto: '+this.user.urlFoto);
                    console.log('url fundo: '+this.user.urlfundo);
                    
                    if (userFoto != '')
                    	this.user.urlFoto = userFoto;
                    if (userFotoThumbs != '')
                    	this.user.urlThumbsFoto = userFotoThumbs;
                    if (userFundo != 'usuari' && userFundo != '')
                    	this.user.urlfundo = userFundo;

                    this.user.isAdmin = false;

                    this.userService.updateUser(this.user).subscribe(
                        data => {

                            console.log(this.user.urlfundo);
                            console.log(JSON.parse(JSON.parse(JSON.stringify(data))._body));
                            this.showNotificationNavegarParaTabelasUsers('top','right', "Usuário salvo com Sucesso!", 2);
                        },
                        error => console.log(error)
                        );



                }, (error) => {
                    console.error(error);
                });

        } else {
            this.showNotification('top','right', "Deve-se incluir uma foto para o usuário.", 3);
        }

    }

	showNotification(from: any, align: any, message: string, color: number) {

		const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

		/*const color = Math.floor((Math.random() * 6) + 1);*/

		$.notify({
			icon: 'notifications',
			message: message,
		}, {
			type: type[color],
			timer: 7000,
			delay: 10000,
			placement: {
				from: from,
				align: align
			},
			onClose: null
		});
	}


	showNotificationNavegarParaTabelasUsers(from: any, align: any, message: string, color: number) {

	    const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

	    /*const color = Math.floor((Math.random() * 6) + 1);*/

	    $.notify({
	        icon: 'notifications',
	        message: message,
	    }, {
	        type: type[color],
	        timer: 3000,
	        delay: 10000,
	        placement: {
	            from: from,
	            align: align
	        },
	        onClosed: this.navegarTabelaUsers()
	    });
	}

	navegarTabelaUsers(){
		this.router.navigate(['/tables/datatables.net']);
	}


	clearFotos(){
		if(this.fileInput.nativeElement)
			this.renderer.setAttribute(this.fileInput.nativeElement, 'value', '');
		this.fileService.resetService();
	}

	fileChangeEventWizard(fileInput: any, ouwner: string){

		if(!fileInput.target.files[0]){
			this.clearFotos();
			this.showNotification('top','right', "Deve-se incluir uma foto para a memória.", 3)
		} else {
			if(!this.fileService.fileChangeEventWizard(fileInput, ouwner)){
				this.clearFotos();
				this.showNotification('top','right', "O aquivo deve ser uma foto.", 3);
			} 

		}

	}

	initComponent(){

        this.fileService.resetService();

        if(localStorage.getItem("currentIdUserEdit") != '') {

            this.userService.getUserById(+localStorage.getItem("currentIdUserEdit")).subscribe(
                userResponse => {
                    this.user = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);
                    console.log("User sendo editado:");
                    console.log(this.user);
                    this.fileService.fileIsInside = true;
                },
                error => console.log(error) 
            );
            
        } else {
            this.user = new User();
        } 

    }

   	getUrlFundo(user: User){
    	return this.hostService.host + user.urlfundo;
 	}

 	getUrlFoto(user: User){
    	return this.hostService.host + user.urlFoto;
 	}

 	getUrlThumbsFoto(user: User){
    	return this.hostService.host + user.urlThumbsFoto;
 	}


}







