import { Component, OnInit, OnChanges, AfterViewInit, SimpleChanges, ElementRef, Renderer2, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { TipoEvento } from '../../models/tipo_evento'
import { FileService } from '../../services/file.service'
import { HostService } from '../../services/host.service'

import { UserService } from '../../services/user.service';
import { DepoimentosService } from '../../services/depoimentos.service';

import { Momento } from '../../models/momento'
import { User } from '../../models/user'
import { Depoimento } from '../../models/depoimento'
import { TipoConteudo } from '../../models/tipo_conteudo'




declare const $: any;
interface FileReaderEventTarget extends EventTarget {
    result: string;
}
interface FileReaderEvent extends Event {
    target: FileReaderEventTarget;
    getMessage(): string;
}


@Component({
	selector: 'app-edicao-depoimento',
	templateUrl: './edicao-depoimento.component.html',
	styleUrls: ['./edicao-depoimento.component.css']
})
export class EdicaoDepoimentoComponent implements OnInit {

	@ViewChild("fileInput") fileInput: ElementRef;
    public depoimentoAtual: Depoimento = new Depoimento();
    public urlDepoimento: string;

    readURL(input: any) {
        if (input.files && input.files[0]) {
            const reader = new FileReader();

            reader.onload = function (e: FileReaderEvent) {
                $('#wizardPicturePreview').attr('src', e.target.result).fadeIn('slow');
            };
            reader.readAsDataURL(input.files[0]);
        }
    }

    constructor(private router: Router, private renderer: Renderer2, private fileService: FileService,
        private depoimentoService: DepoimentosService, private userService: UserService, private hostService: HostService) { }

    ngOnInit() {

        this.initComponent();

        // Code for the Validator
        const $validator = $('.wizard-card form').validate({
            rules: {
                firstname: {
                    required: true,
                    minlength: 3
                },
                titulo: {
                    required: true,
                    minlength: 3
                },
                data: {
                    required: true,
                    minlength: 3,
                }
            },

            errorPlacement: function (error: any, element: any) {
                $(element).parent('div').addClass('has-error');
            }
        });

        // Wizard Initialization
        $('.wizard-card').bootstrapWizard({
            'tabClass': 'nav nav-pills',
            'nextSelector': '.btn-next',
            'previousSelector': '.btn-previous',

            onNext: function (tab: any, navigation: any, index: any) {
                const $valid = $('.wizard-card form').valid();
                if (!$valid) {
                    $validator.focusInvalid();
                    return false;
                }
            },

            onInit: function (tab: any, navigation: any, index: any) {

                // check number of tabs and fill the entire row
                const $total = navigation.find('li').length;
                let $width = 100 / $total;
                const $wizard = navigation.closest('.wizard-card');

                const $display_width = $(document).width();

                if ($display_width < 600 && $total > 3) {
                    $width = 50;
                }

                navigation.find('li').css('width', $width + '%');
                const $first_li = navigation.find('li:first-child a').html();
                const $moving_div = $('<div class="moving-tab">' + $first_li + '</div>');
                $('.wizard-card .wizard-navigation').append($moving_div);

                // this.refreshAnimation($wizard, index);
                const total_steps = $wizard.find('li').length;
                let move_distance = $wizard.width() / total_steps;
                const step_width = move_distance;
                move_distance *= index;

                const $current = index + 1;

                if ($current === 1) {
                    move_distance -= 8;
                } else if ($current === total_steps) {
                    move_distance += 8;
                }

                $wizard.find('.moving-tab').css('width', step_width);
                $('.moving-tab').css({
                    'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                    'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'

                });

                $('.moving-tab').css('transition', 'transform 0s');
            },

            onTabClick: function (tab: any, navigation: any, index: any) {

                const $valid = $('.wizard-card form').valid();

                if (!$valid) {
                    return false;
                } else {
                    return true;
                }
            },

            onTabShow: function (tab: any, navigation: any, index: any) {
                const $total = navigation.find('li').length;
                let $current = index + 1;

                const $wizard = navigation.closest('.wizard-card');

                // If it's the last tab then hide the last button and show the finish instead
                if ($current >= $total) {
                    $($wizard).find('.btn-next').hide();
                    $($wizard).find('.btn-finish').show();
                } else {
                    $($wizard).find('.btn-next').show();
                    $($wizard).find('.btn-finish').hide();
                }

                const button_text = navigation.find('li:nth-child(' + $current + ') a').html();

                setTimeout(function () {
                    $('.moving-tab').text(button_text);
                }, 150);

                const checkbox = $('.footer-checkbox');

                if (index !== 0) {
                    $(checkbox).css({
                        'opacity': '0',
                        'visibility': 'hidden',
                        'position': 'absolute'
                    });
                } else {
                    $(checkbox).css({
                        'opacity': '1',
                        'visibility': 'visible'
                    });
                }

                // this.refreshAnimation($wizard, index);
                const total_steps = $wizard.find('li').length;
                let move_distance = $wizard.width() / total_steps;
                const step_width = move_distance;
                move_distance *= index;

                $current = index + 1;

                if ($current === 1) {
                    move_distance -= 8;
                } else if ($current === total_steps) {
                    move_distance += 8;
                }

                $wizard.find('.moving-tab').css('width', step_width);
                $('.moving-tab').css({
                    'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                    'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'
                });
            }
        });


        // Prepare the preview for profile picture
        $('#wizard-picture').change(function () {
            this.readURL(this);
        });

        $('[data-toggle="wizard-radio"]').click(function () {
            const wizard = $(this).closest('.wizard-card');
            wizard.find('[data-toggle="wizard-radio"]').removeClass('active');
            $(this).addClass('active');
            $(wizard).find('[type="radio"]').removeAttr('checked');
            $(this).find('[type="radio"]').attr('checked', 'true');
        });

        $('[data-toggle="wizard-checkbox"]').click(function () {
            if ($(this).hasClass('active')) {
                $(this).removeClass('active');
                $(this).find('[type="checkbox"]').removeAttr('checked');
            } else {
                $(this).addClass('active');
                $(this).find('[type="checkbox"]').attr('checked', 'true');
            }
        });

        $('.set-full-height').css('height', 'auto');

    }

    ngOnChanges(changes: SimpleChanges) {
        const input = $(this);
        const target: EventTarget = null;
        if (input.files && input.files[0]) {
            const reader: any = new FileReader();

            reader.onload = function (e: any) {
                $('#wizardPicturePreview').attr('src', e.target.result).fadeIn('slow');
            };
            reader.readAsDataURL(input.files[0]);
        }
    }
    ngAfterViewInit() {
        $('.wizard-card').each(function () {

            const $wizard = $(this);
            const index = $wizard.bootstrapWizard('currentIndex');
            // this.refreshAnimation($wizard, index);

            const total_steps = $wizard.find('li').length;
            let move_distance = $wizard.width() / total_steps;
            const step_width = move_distance;
            move_distance *= index;

            const $current = index + 1;

            if ($current === 1) {
                move_distance -= 8;
            } else if ($current === total_steps) {
                move_distance += 8;
            }

            $wizard.find('.moving-tab').css('width', step_width);
            $('.moving-tab').css({
                'transform': 'translate3d(' + move_distance + 'px, 0, 0)',
                'transition': 'all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1)'

            });

            $('.moving-tab').css({
                'transition': 'transform 0s'
            });
        });
    }


    goToDepoimentos() {
        this.router.navigate(['app-lista-depoimento']);
    }

    clearFotos() {
        if (this.fileInput.nativeElement)
            this.renderer.setAttribute(this.fileInput.nativeElement, 'value', '');
        this.fileService.resetService();
    }


    showNotification(from: any, align: any, message: string, color: number) {

        const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

        /*const color = Math.floor((Math.random() * 6) + 1);*/

        $.notify({
            icon: 'notifications',
            message: message,
        }, {
                type: type[color],
                timer: 7000,
                delay: 10000,
                placement: {
                    from: from,
                    align: align
                },
                onClose: null
            });
    }


    fileChangeEventWizard(fileInput: any, ouwner: string) {

        if (!fileInput.target.files[0]) {
            this.clearFotos();
            this.showNotification('top', 'right', "Deve-se incluir uma foto para o depoimento.", 3)
        } else {
            if (!this.fileService.fileChangeEventWizard(fileInput, ouwner)) {
                this.clearFotos();
                this.showNotification('top', 'right', "O aquivo deve ser uma foto.", 3);
            }

        }

    }


    salvarDepoimento() {
        if (this.fileService.fileConteudoIsInside || this.urlDepoimento != '') {

            (<HTMLInputElement> document.getElementById("salvar")).disabled = true;

            this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
                userResponse => {

                    this.depoimentoAtual.user = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);

                    this.fileService.upload().then(
                        (result) => {
                            let nomesArquivosSalvosTemp = localStorage.getItem("nomesArquivosSalvosTemp");

                            console.log("Nomes dos arquivos salvos:")
                            console.log(nomesArquivosSalvosTemp);
                            let urlDepoimentoAvatar = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("depoimento_avatar:")+18, nomesArquivosSalvosTemp.indexOf("/"));
                            let urlThumbsDepoimentoAvatar;
                            this.urlDepoimento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/depoimento:")+12, nomesArquivosSalvosTemp.indexOf("/"));
                            let urlThumbsDepoimento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/thumbs_depoimento:") + 19, nomesArquivosSalvosTemp.lastIndexOf("/"));;

                            if (nomesArquivosSalvosTemp.includes("depoimento_avatar:")) {
                                urlDepoimentoAvatar = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("depoimento_avatar:") + 18,
                                    nomesArquivosSalvosTemp.indexOf("/"));

                                if (this.getTipoConteudo(urlDepoimentoAvatar) == TipoConteudo.foto) {
                                    urlThumbsDepoimentoAvatar = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/thumbs_depoimento_avatar:") + 26, nomesArquivosSalvosTemp.lastIndexOf("/depoimento:"));
                                    this.depoimentoAtual.urlAvatarThumbs = urlThumbsDepoimentoAvatar;
                                }

                                this.depoimentoAtual.urlAvatar = urlDepoimentoAvatar;
                            }


                            if (nomesArquivosSalvosTemp.includes("/depoimento:")) {
                                this.urlDepoimento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/depoimento:") + 12,
                                    nomesArquivosSalvosTemp.indexOf("/thumbs_depoimento:"));
                                console.log("this.urldepoimento");
                                console.log(nomesArquivosSalvosTemp);
                                console.log(this.urlDepoimento);

                                this.depoimentoAtual.tipoConteudo = this.getTipoConteudo(this.urlDepoimento);

                                if (this.getTipoConteudo(this.urlDepoimento) == TipoConteudo.foto) {
                                    urlThumbsDepoimento = nomesArquivosSalvosTemp.substring(nomesArquivosSalvosTemp.indexOf("/thumbs_depoimento:") + 19, nomesArquivosSalvosTemp.lastIndexOf("/"));
                                    this.depoimentoAtual.urlConteudoThumbs = urlThumbsDepoimento;
                                }

                                this.depoimentoAtual.urlConteudo = this.urlDepoimento;



                            }

                            this.depoimentoService.saveDepoimento(this.depoimentoAtual).subscribe(
                                depoimento => {
                                    let depoimentoResponse: Depoimento = JSON.parse(JSON.parse(JSON.stringify(depoimento))._body);
                                    this.depoimentoAtual = new Depoimento();
                                    this.depoimentoAtual.user = depoimentoResponse.user;
                                    this.showNotification('top', 'right', "Depoimento Salvo com Sucesso!", 2);
                                    this.goToDepoimentos();
                                }, error => console.log(error));

                        }, (error) => {
                            console.error(error);
                        });



                }, error => console.log(error)
            );


        } else {
            this.showNotification('top', 'right', "Deve-se incluir uma foto para o depoimento.", 3);

        }
    }

    getTipoConteudo(filename: string): TipoConteudo {
        let tipoConteudo: TipoConteudo = /\.(jpg)$/i.test(filename) || /\.(jpeg)$/i.test(filename) || /\.(png)$/i.test(filename) ?
            TipoConteudo.foto : /\.(mpeg)$/i.test(filename) || /\.(mpg)$/i.test(filename) || /\.(mp4)$/i.test(filename) ?
                TipoConteudo.video : null;
        return tipoConteudo;
    }


    getUrlConteudo(){
        
        
                if(this.depoimentoAtual){
                    if(this.depoimentoAtual.urlConteudo  && (this.depoimentoAtual.tipoConteudo == 0))
                    {
                        return this.hostService.host + this.depoimentoAtual.urlConteudo;
                    } else if(!this.depoimentoAtual.urlConteudo || this.depoimentoAtual.urlConteudo == ''){
                        return "../../assets/img/image_placeholder.jpg";
                    }
                    return "../../assets/img/thumbnail-video.png";
                }
        
                return "../../assets/img/image_placeholder.jpg";
            }

	initComponent(){

        this.fileService.resetService();

        console.log("DEPOIMENTO ATUAL: ");
        console.log(localStorage.getItem("currentIdDepoimentoEdit"));

        if(localStorage.getItem("currentIdDepoimentoEdit") != '') {

            this.depoimentoService.getDepoimentoById(+localStorage.getItem("currentIdDepoimentoEdit")).subscribe(
                depoimentoResponse => {
                    this.depoimentoAtual = JSON.parse(JSON.parse(JSON.stringify(depoimentoResponse))._body);
                    console.log("Depoimento sendo editado:");
                    console.log(this.depoimentoAtual);
                    this.fileService.fileIsInside = true;
                },
                error => console.log(error) 
            );
            
        } else {
            this.depoimentoAtual = new Depoimento();
        } 

    }

 	getUrlFoto(depoimento: Depoimento){
    	return this.hostService.host + depoimento.urlAvatar;
 	}


}







